// ReactDOMServer.renderToString(thing_to_render) will greatly improve page load times.

import React from 'react';
import './App.css';

class Display extends React.Component {
  render() {
    return (
      <div id="display" className="container">
        <DrumMachine />
      </div>
    );
  }
};

function importAll(r) {
  return r.keys().map(r);
}

// cannot wrap this in a function -- handled compile-time, not run-time
const samples = importAll(require.context('./Audio', false, /\.wav$/i));

function playNote(note) {
  var audio = new Audio();
  audio.loop = false;

  switch(note) {
    case 'q':
      audio.src = samples[0];
      break;
    case 'w':
      audio.src = samples[1];
      break;
    case 'e':
      audio.src = samples[2];
      break;
    case 'a':
      audio.src = samples[3];
      break;
    case 's':
      audio.src = samples[4];
      break;
    case 'd':
      audio.src = samples[5];
      break;
    case 'z':
      audio.src = samples[6];
      break;
    case 'x':
      audio.src = samples[7];
      break;
    case 'c':
      audio.src = samples[8];
      break;
    default:
      audio.src = samples[9];
  }
  audio.play();
}

class DrumPad extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.handlePress = this.handlePress.bind(this);
  }

  handleClick(event) {
    playNote(this.props.letter);
  }

  handlePress(event) {
    var clickedBtn = document.getElementById("drum-pad-"+event.key);
    if(clickedBtn) {
      clickedBtn.classList.add("drum-pad-active");
      window.setTimeout(function() {
        clickedBtn.classList.remove("drum-pad-active");
      }, 250);
    }
    playNote(event.key);
  }

  render() {
    return (
      <button id={"drum-pad-"+this.props.letter} className="drum-pad col-3 my-4 btn btn-secondary" onClick={this.handleClick.bind(this)} onKeyPress={this.handlePress.bind(this)}>{this.props.letter}</button>
    );
  }
};

class DrumMachine extends React.Component {
  render() {
    return (
      <div id="drum-machine">
        <div className="row justify-content-around">
          <DrumPad letter="q" />
          <DrumPad letter="w" />
          <DrumPad letter="e" />
        </div>
        <div className="row justify-content-around">
          <DrumPad letter="a" />
          <DrumPad letter="s" />
          <DrumPad letter="d" />
        </div>
        <div className="row justify-content-around">
          <DrumPad letter="z" />
          <DrumPad letter="x" />
          <DrumPad letter="c" />
        </div>
      </div>
    );
  }
};

